.row_page{ margin-top: 100px!important;}
.box-part1{ max-height:240px !important;}

.overlay_vendor { height: 100vh !important; display: none; position: fixed; top: 0px; left: 0px; width: 100%; overflow-y: auto; background:rgba(0, 0, 0, 0.6); z-index: 99;}
.overlay_vendor form { background:#fff; width: 500px; height: auto; position: absolute; left: 50%; top: 20%; display: inline-block; transform: translateX(-50%); -moz-transform: translateX(-50%); -webkit-transform: translateX(-50%); -ms-transform: translateX(-50%);  }
.overlay_vendor form .closebtn_vendor { position: absolute; top: 10px; right: 10px; display: inline-block; padding: 0px 0px; line-height: normal; height: auto; font-size: 24px;}



/*add css by rohit kumar*/
.vendor_ser_det {  }
.vendor_ser_det .container { padding: 0px 15px; }
.vendor_ser_det .table-responsive { overflow-y: visible; overflow-x: visible; margin-bottom: 20px; }
.vendor_ser_det table { width: 100%; margin-top: 30px; }
.vendor_ser_det table thead th,
.vendor_ser_det table tbody td { white-space: normal; padding: 5px 10px; text-align: center; border: 1px solid #ddd; vertical-align: middle;}
.vendor_ser_det table thead th { color:#fff !important; font-size: 18px; line-height: 34px;font-weight: 600; background:#1b2e42; }
.vendor_ser_det table tbody td { line-height: 20px; padding: 10px 10px; }
.vendor_ser_det table tbody tr:nth-child(even) td { background:#f1f1f1; }
.vendor_ser_det table tbody td div i { display: inline-block; color:#333 !important; line-height: 15px; width: auto !important; padding: 5px; position: relative !important; margin: 0px !important; }
.vendor_ser_det .gap h4 button { padding: 10px 10px !important; font-weight: 600; }


@media (max-width: 767px) {
  .vendor_ser_det .table-responsive { overflow-x:auto; }
  .vendor_ser_det table thead th { line-height: 20px !important;}
  .vendor_ser_det table thead th, .vendor_ser_det table tbody td { font-size: 14px !important; padding: 5px 5px;  }
  .vendor_ser_det h4 { font-size: 22px; }
  .overlay_vendor form { width: 300px; }
}
@media (max-width: 479px) {
  .vendor_ser_det table thead th, .vendor_ser_det table tbody td { font-size: 12px !important; }
}

